.social .social-item {
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 0 30px;
  display: flex;
}

.social .social-item .social-link {
  text-align: center;
  background: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .4s;
  display: flex;
  position: relative;
  transform: perspective(1000px)rotate(-30deg)skew(25deg)translate(0);
}

.social .social-item .social-link:before {
  content: "";
  background: #b1b1b1;
  width: 10px;
  height: 100%;
  transition: all .4s;
  position: absolute;
  top: 5px;
  left: -10px;
  transform: rotate(0)skewY(-45deg);
}

.social .social-item .social-link:after {
  content: "";
  background: #b1b1b1;
  width: 100%;
  height: 10px;
  transition: all .4s;
  position: absolute;
  top: 40px;
  left: -5px;
  transform: rotate(0)skewX(-45deg);
}

.social .social-item .social-link:hover {
  transform: perspective(1000px)rotate(-30deg)skew(25deg)translate(5px, -5px);
  box-shadow: -20px 20px 10px #00000080;
}

.social .social-item:first-child a {
  color: #3b5999;
}

.social .social-item:first-child:hover a, .social .social-item:first-child:hover a:before, .social .social-item:first-child:hover a:after {
  background: #3b5999;
}

.social .social-item:nth-child(2) a {
  color: #55acee;
}

.social .social-item:nth-child(2):hover a, .social .social-item:nth-child(2):hover a:before, .social .social-item:nth-child(2):hover a:after {
  background: #55acee;
}

.social .social-item:nth-child(3) a {
  color: #757575;
}

.social .social-item:nth-child(3):hover a, .social .social-item:nth-child(3):hover a:before, .social .social-item:nth-child(3):hover a:after {
  background: #757575;
}

.social .social-item:nth-child(4) a {
  color: #e4405f;
}

.social .social-item:nth-child(4):hover a, .social .social-item:nth-child(4):hover a:before, .social .social-item:nth-child(4):hover a:after {
